import './button.css';


export const createButton = ({
  primary = false,
  size = 'medium',
  adjacentColor = 'black',
  label,
  onClick,
}) => {
  const colors = [""]

  const btn = document.createElement('button');
  btn.type = 'button';
  btn.innerText = label;
  btn.addEventListener('click', onClick);

  const mode = primary ? 'wonoly-button--primary' : 'wonoly-button--secondary';
  btn.className = ['wonoly-button', `wonoly-button--${size}`, mode].join(' ');

  if (primary) {
    btn.style.backgroundColor = adjacentColor;
  } else {
    btn.style.border = `1px solid ${adjacentColor}`;
  }

  return btn;
};
